/* Navbar CSS */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: #121B28;
    color: #FAEDCD;
    border-radius: 10px;
    margin: 10px 10px 0px 10px;
  
    h1 {
      margin: 0;
    }

    button {
      float: right;
      background-color: #2A9D8F;
      color: #E9EDC9;
      padding: 0.5rem 1rem;
      border: none;
      border-radius: 5px;
      font-size: 1rem;
      cursor: pointer;
      transition: all 0.2s ease-in-out;

      &:hover {
        background-color: #F4A261;
        color: #FFC8DD;
      }
        
    }
    .center-text {
      margin: 0 auto;
      text-align: center;
    }
    
}
  
  /* User Card CSS */
  .card-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 3rem;
    padding: 7rem;
    padding-top: 2rem;
    justify-content: center;
    align-items: center;
  
    .card {
      background-color: #DEE2FF;
      padding: 3rem;
      border-radius: 10px;
      text-align: center;
      transition: all 0.2s ease-in-out;
  
      img {
        width: 90%;
        height: auto;
        border-radius: 10px;
        object-fit: cover;
        transition: all 0.2s ease-in-out;

        &:hover {
          box-shadow: 0px 0px 10px #ccc;
          transform: scale(1.1);
        }
      }

      &:hover {
        box-shadow: 0px 0px 10px #ccc;
        transform: scale(1.1);
      }
    }
    
    h2 {
      display: block;
      font-size: 1.5em;
      margin-block-start: 0.83em;
      margin-block-end: 0.83em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      font-weight: bold;
      color: #264653;
    }
  
  }

  footer {
    background: #AEE5D0;
    color: #425C81;
    text-align: center;
    padding: 1rem;
    font-weight: bold;
  }

  
  